import React from 'react'
import barcode from '.././images/barcode.png'

function Home() {
    
    return (
        <React.Fragment>
            <div className="home container" >
                <header>
                    <div className="logo">
                        <i className="fas fa-circle"></i><i className="fas fa-circle"></i><i className="fas fa-circle"></i>
                    </div>
                </header>
                <section className="hero">
                    <h1 className='text'>Hey There.<br/>
                        Welcome to<br/>
                        Code of Colors</h1>
                </section>
                <footer>
                    <div className="barcode-wrapper">
                        <img src={barcode} className='barcode' alt="barcode"/>
                        <img src={barcode} className='barcode' alt="barcode"/>
                        <img src={barcode} className='barcode' alt="barcode"/>
                    </div>
                </footer>
            </div>
        </React.Fragment>
    )
}
export default Home;
