import React, {useRef} from 'react';
import './App.css';
import axios from 'axios';

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import Home from './components/Home';
import Gallery from './components/Gallery';
import Codes from './components/Codes';
import Creatives from './components/Creatives';
import About from './components/About';

function App() {

    const [colors, setcolors] = React.useState([]);
    const sidenav = useRef(null);
    const overlay = useRef(null);
    const home = useRef(null);
    const url = 'https://apicenter.devsolo.tech/api/colors';
    const token = 'JZTCTvJlW15d6GoOjZPyyuuP648ehO0QGkoDaxJm' ;

    function openNav() {
        sidenav.current.style.width = "280px";
        home.current.style.filter = 'blur(1.2px)';
        overlay.current.style.display = 'inline';
    }
    
    function closeNav() {
        sidenav.current.style.width = "0px";
        home.current.style.filter = 'blur(0px)';
        overlay.current.style.display = 'none';
    }

  const appHeight = () => {
    const doc = document.body
    // doc.style.height = `${window.innerHeight}px`;
    doc.style.height = `100vh`;
  }
// window.addEventListener('resize', appHeight)
appHeight()
   React.useEffect(()=>{
    async function fetchColors() {
      try {
        
          const response = await axios.get(url,{headers: {
          'Accept' : 'application/json',
          'Content-Type' : 'application/json',
          'Authorization': `Bearer ${token}` 
        }});
        setcolors(response.data);
      } catch (error) {
        console.error(error);
      }
    }
    fetchColors();
   },[])
  return (
    <Router>
      <div className='fake-home' ref={home}>
      <Switch>
        <Route path="/about">
          <About />
        </Route>
        <Route path="/gallery">
          <Gallery />
        </Route>
        <Route path='/codes'>
          <Codes colors={colors}/>
        </Route>
        <Route path="/creatives">
          <Creatives />
        </Route>
        <Route path="/" component={Home}/>
      </Switch>  
      </div>
      <div className="blur" ref={overlay}></div>
      <div ref={sidenav} className="sidenav container">
          <div className="nav-link-wrapper">
              <Link onClick={closeNav} to='/' className="nav-link">Home</Link>
              <Link onClick={closeNav} to='/codes' className="nav-link">Codes</Link>
              <Link onClick={closeNav} to='/gallery' className="nav-link">Gallery</Link>
              <Link onClick={closeNav} to='/creatives' className="nav-link">Creatives</Link>
              <Link onClick={closeNav} to='/about' className="nav-link">About</Link>
          </div>
          <button href="/#" className="close-btn" onClick={closeNav}>
              <i className="fas fa-times"></i>
          </button>
      </div>

    {/* menu-button */}
    <button className="menu-btn" onClick={openNav}>
      <i className="fa fa-bars" aria-hidden="true"></i>
    </button>

    </Router>
  );
}

export default App;
