import React, {useState} from 'react'

function Creatives() {

    const [firstColor, setfirstColor] = useState(`#FF9933`)
    const [secondColor, setsecondColor] = useState(`#FFFFFF`)
    const [thirdColor, setthirdColor] = useState(`#138808`)

    function setfirst(e) {
        setfirstColor(e.target.value);
    }
    function setSecond(e) {
        setsecondColor(e.target.value);
    }
    function setThird(e) {
        setthirdColor(e.target.value);
    }


    return (
        <section className='creatives p-15'>
            <div className="page-title">
                Creatives
            </div>         
            <div className="content">
                <div className="card h-auto" style={{backgroundImage:`linear-gradient(to right, ${firstColor} , ${secondColor}, ${thirdColor})`}}>
                    {`${firstColor}, ${secondColor}, ${thirdColor}`}
                </div>
                <div className="color-input mt-1">
                    <input type="color" onChange={(e) => {setfirst(e)}} name="" value={firstColor} id=""/>
                    <input type="color" onChange={(e) => {setSecond(e)}} name="" value={secondColor} id=""/>
                    <input type="color" onChange={(e) => {setThird(e)}} name="" value={thirdColor} id=""/>
                </div>
                <div className="card h-auto">
                    <p>backgroundImage:`linear-gradient(to right, {firstColor} ,{secondColor}, {thirdColor})`</p>
                </div>
                <div className="card h-auto" style={{backgroundImage:`linear-gradient(to right, rgba(255, 0, 0, 0), ${firstColor})`}}>
                    {firstColor}
                </div>
                <div className="card h-auto" style={{backgroundImage:`linear-gradient(to right, rgba(255, 0, 0, 0), ${secondColor})`}}>
                    {secondColor}
                </div>
                <div className="card h-auto" style={{backgroundImage:`linear-gradient(to right, rgba(255, 0, 0, 0), ${thirdColor})`}}>
                    {thirdColor}
                </div>
                <div className="">
                    <b>Soft Shadow :</b>
                    <p>
                    -webkit-box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.75);<br/>
                    -moz-box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.75);<br/>
                    box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.75);
                    </p>
                </div>
            </div>
        </section>
    )
}

export default Creatives
