import React ,{useState} from 'react'

function ColorCard(props) {

    const [btnText, setbtnText] = useState('Copy')

    function copyToClipboard() {
       var dummy = document.createElement("textarea");
        document.body.appendChild(dummy);
        dummy.value = props.color;
        dummy.select();
        document.execCommand("copy");
        document.body.removeChild(dummy);
        setbtnText('Copied!')
    }

    return (
        <div className="color card copy-hover" style={{ backgroundColor:props.color, border:'0.1px solid rgb(198, 198, 198)'}}>
            <div className="text-btn-wrapper">
                <div className="color-code">{props.color}</div>
                <button className="copy-btn" onClick={copyToClipboard}>{btnText}</button>
                {(props.info) ? 
                <button className="conner-btn tooltip"><i className="fa fa-info-circle" aria-hidden="true"></i>
                    <span className="tooltiptext">{props.info}</span>
                </button>
                : ""
            }
            </div>
        </div>
    )
}

export default ColorCard
